@import "../../style/globals";

// Menu
// --------------------------------------------------

.#{$prefix}-menu {
  
  //position: absolute;
  position: fixed;
  z-index: 0;
  display: none;
  overflow: hidden;
  opacity: 1;
  contain: strict;
  
  @include position(0, 0, 0, 0);
}

.#{$prefix}-menu.show-menu {
  display: block;
}

.menu-inner {
  //@include transform(translate3d(-9999px, 0, 0));
  
  //position: absolute;
  position: fixed;
  z-index: 10;
  display: block;
  width: $menu-width;
  height: 100%;
  overflow: scroll;
  
  // inner
  //transform: translate3d(0, 0, 0);
  transition: all cubic-bezier(0, 0, 0, 1) 280ms;
  -webkit-overflow-scrolling: touch;
  
  @include position(0, auto, 0, 0);
  //contain: layout style;
}

.#{$prefix}-menu[side=right] > .menu-inner {
  right: 0;
  left: auto;
}

.#{$prefix}-menu[side=end] > .menu-inner {
  @include position-horizontal(auto, 0);
}

.#{$prefix}-menu .#{$prefix}-backdrop {
  z-index: -1;
}

.menu-content {
  min-height: 100%; // add for box layout
  transition: all cubic-bezier(0, 0, 0.6, 1) 280ms;
}

.menu-content-open {
  cursor: pointer;
  touch-action: manipulation;
}

.menu-content-open .#{$prefix}-pane,
.menu-content-open .#{$prefix}-content,
.menu-content-open .toolbar {
  // the containing element itself should be clickable but
  // everything inside of it should not clickable when menu is open
  pointer-events: none;
}

.menu-content-reveal.#{$prefix}-nav {
  position: absolute;
}

.menu-content-reveal.menu-content-open {
  transform: translateX(304px);
  @media (max-width: 340px) {
    transform: translateX(264px);
  }
}

.menu-content-reveal.menu-content-right.menu-content-open {
  transform: translateX(-304px);
  @media (max-width: 340px) {
    transform: translateX(-264px);
  }
}

@media (max-width: 340px) {
  .menu-inner {
    width: $menu-small-width;
  }
}

@media (max-width: 340px) {
  .menu-inner {
    width: $menu-small-width;
  }
  
}

// Menu Reveal
// --------------------------------------------------
// The content slides over to reveal the menu underneath.
// The menu itself, which is under the content, does not move.

// Menu Overlay
// --------------------------------------------------
// The menu slides over the content. The content
// itself, which is under the menu, does not move.

.#{$prefix}-menu[type=overlay].show-menu {
  z-index: $z-index-overlay + $z-index-overlay-sheet;
}

// Menu Animate
// --------------------------------------------------

/* slide-in-left */

/* animate class */
.slide-in-left-enter-active,
.slide-in-left-leave-active {
  transform: translateX(0);
}

.slide-in-left-enter,
.slide-in-left-leave-active {
  transition: transform cubic-bezier(0.2, 0, 1, 1) 280ms;
  transform: translateX(-100%);
}

/* slide-in-right */

/* animate class */
.slide-in-right-enter-active,
.slide-in-right-leave-active {
  transform: translateX(0);
}

.slide-in-right-enter,
.slide-in-right-leave-active {
  transition: transform cubic-bezier(0, 0, 1, 1) 280ms;
  transform: translateX(100%);
}



