@charset "UTF-8"; @import "/Users/Hsiang/GitHub/vimo/demo/src/theme/variables.scss";
.vm-scroll.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  .scroller {
    position: absolute;
    z-index: 1;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    width: 100%;
    transform: translateZ(0);
    user-select: none;
    text-size-adjust: none;
  }
}



