@import "../../style/globals";

// Action Sheet
// --------------------------------------------------

.#{$prefix}-action-sheet {
  position: fixed;
  z-index: $z-index-overlay + $z-index-overlay-sheet;
  display: block;
  width: $action-sheet-width;
  height: $action-sheet-width;
  
  @include position(0, null, null, 0);
}

.action-sheet-wrapper {
  position: absolute;
  z-index: $z-index-overlay-wrapper;
  display: block;
  width: $action-sheet-width;
  max-width: $action-sheet-max-width;
  pointer-events: none;
  transition: all cubic-bezier(0.36, 0.66, 0.04, 1) 400ms;
  
  @include position(null, 0, 0, 0);
  @include margin(auto);
}

.action-sheet-button {
  width: $action-sheet-width;
}

.action-sheet-container {
  display: flex;
  height: 100%;
  max-height: 100%;
  flex-flow: column;
  justify-content: flex-end;
}

.action-sheet-group-normal {
  overflow: scroll;
  pointer-events: all;
  flex-shrink: 2;
  
  .buttons-inner {
    position: relative;
    z-index: 1;
    max-height: 265px;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
  }
}

.action-sheet-group-cancel {
  overflow: hidden;
  flex-shrink: 0;
}

// Action Sheet Transition
// --------------------------------------------------

.action-sheet-enter-active,
.action-sheet-leave-active {
  transform: translateY(0%);
}

.action-sheet-enter,
.action-sheet-leave-active {
  transform: translateY(100%);
}



