@charset "UTF-8"; @import "/Users/Hsiang/GitHub/vimo/demo/src/theme/variables.scss";
@import "../globals.ios";

// iOS Note
// --------------------------------------------------

/// @prop - Text color of the note
$note-ios-color: darken($list-ios-border-color, 10%) !default;

.note-ios {
  color: $note-ios-color;
}

// Generate iOS Note Colors
// --------------------------------------------------

@each $color-name, $color-base, $color-contrast in get-colors($colors-ios) {
  .note-ios-#{$color-name} {
    color: $color-base;
  }

}



