.#{$prefix}-img {
  display: inline-block;
  min-width: 20px;
  min-height: 20px;
  background: $img-placeholder-background;
  contain: strict;
}

.#{$prefix}-img img {
  transition: opacity 0.5s;
  object-fit: cover;
}

