// Range
// --------------------------------------------------

.item-range .item-inner {
  width: 100%;
  overflow: visible;
}

.item-range .input-wrapper {
  width: 100%;
  overflow: visible;
  flex-direction: column;
}

.item-range .#{$prefix}-range {
  width: 100%;
}

.item-range .#{$prefix}-range .#{$prefix}-label {
  align-self: center;
}

.#{$prefix}-range {
  position: relative;
  display: flex;
  align-items: center;
  
  z-index: 10;
}

.#{$prefix}-range .#{$prefix}-label {
  flex: initial;
}

.#{$prefix}-range .#{$prefix}-icon {
  min-height: 24px;
  font-size: 24px;
  line-height: 1;
}

.range-slider {
  position: relative;
  cursor: pointer;
  flex: 1;
}

.range-knob-handle {
  touch-action: pan-x;
}



