@import "./style/slides";

.swiper-container {
  display: flex;
  width: 100%;
  height: 100%;
  padding: 0;
  overflow: hidden;
  
  .swiper-wrapper {
    display: flex;
    width: 100%;
    height: 100%;
    padding: 0;
    
    .swiper-slide {
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      font-size: inherit;
      
      img {
        width: auto;
        max-width: 100%;
        height: auto;
        max-height: 100%;
      }
    }
  }
}



